html,
body,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --dis: 50px;
}
* {
  box-sizing: border-box;
}
.color {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 960px;
  display: flex;
  justify-content: space-between;
}
.color-table {
  display: grid;
  grid-template-columns: repeat(6, 68px);
  grid-template-rows: repeat(6, 68px);
  justify-items: center;
  align-items: center;
}
.color-drawer {
  width: 58.3%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.color-drawer__card {
  flex: 1;
  position: relative;
  overflow: hidden;
  width: 500px;
  margin: 0 auto;
}
.color-drawer__card .animate {
  animation: up 0.5s ease-in-out 1;
}
.color-drawer__card .animate.active {
  animation: activeUp 0.5s ease-in-out 1;
}
.color-drawer__card div {
  position: absolute;
  top: 155px;
  width: 120px;
  height: 120px;
  transform: rotate(45deg);
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
}
.color-drawer__card div:hover {
  top: 142px;
  width: 150px;
  height: 150px;
}
.color-drawer__card div.active {
  top: 142px;
  width: 150px;
  height: 150px;
}
.color-drawer__card .orange {
  left: var(--dis);
}
.color-drawer__card .yellow {
  left: calc(var(--dis) * 2);
}
.color-drawer__card .green {
  left: calc(var(--dis) * 3);
}
.color-drawer__card .cyanblue {
  left: calc(var(--dis) * 4);
}
.color-drawer__card .blue {
  left: calc(var(--dis) * 5);
}
.color-drawer__card .purple {
  left: calc(var(--dis) * 6);
}
.color-drawer__card .grey {
  left: calc(var(--dis) * 7);
}
@media only screen and (max-width: 500px) {
  .wrapper {
    flex-direction: column-reverse;
  }
}
@media only screen and (max-width: 768px) {
  .wrapper {
    width: 90%;
    flex-direction: column-reverse;
  }
}
@keyframes up {
  from {
    top: 205px;
  }
  to {
    top: 155px;
  }
}
@keyframes activeUp {
  from {
    top: 205px;
  }
  to {
    top: 142px;
  }
}
