.title {
  padding-top: 70px;
  font-size: 120px;
  text-align: center;
}
.title svg {
  margin-right: 20px;
  width: 70px;
  height: 70px;
  line-height: 120px;
  cursor: pointer;
  transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  animation: show 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}
.title svg:last-child {
  margin-right: 0;
}
.title svg:hover {
  transform: rotateY(180deg);
}
@keyframes show {
  0 {
    transform: scale(0);
  }
  50% {
    transform: scale(2);
  }
  100% {
    transform: scale(1);
  }
}
